import interfaces from '../../utils/interfaces.js';
var common = require('../../utils/common.js');
import app from "../../App.vue";
export default {
	data() {
		return {
			master_info: '',
			offer: [],
			order: [],
			order_no: '',
			showModalStatus: false,
		}
	},
	onLoad(options) {
		this.order_no = options.order_no ? options.order_no : '';
	},
	onShow() {
		if (app.isAuth()) {
			this.master_info = app.globalData.master_info;
			this.loadData();
		}
	},
	methods: {
		//获取订单详情
		loadData() {
			this.request({
				url: interfaces.orderPriceList,
				data: {
					order_no: this.order_no,
				},
				header: {
					token: this.master_info.token,
				},
				method: 'POST',
				success: res => {
					console.log(res);
					if (res.code == 200) {
						this.order = res.data.order;
						this.offer = res.data.offer;
					} else {
						app.isLogin(res.code, res.msg);
					}
				}
			})
		},
		// 显示遮罩层
		// 下单工单
		showModal(e) {
			this.showModalStatus= true;
		},
		//隐藏对话框
		closeModal() {
			this.showModalStatus = false;
		},
		//form表单提交修改尾款
		formSubmit(e) {
			var data = e.detail.value;
			data.order_no=this.order_no;
			//这个判断有点难处理  要搞一搞
			if (data.offer_price=="") {
				wx.showToast({
					title: '请输入材料名称',
					icon: "none"
				});
				return;
			}
			if (!(Number(data.offer_price) >= 0)) {
				wx.showToast({
					title: '请输入正确的材料费',
					icon: "none"
				});
				return;
			}
			this.request({
				url: interfaces.orderAddPrice,
				data: data,
				header:{
					token:this.master_info.token
				},
				method: 'POST',
				success: ((res) => {
					if (res.code == 200) {
						wx.showToast({
							title: res.msg,
							icon: 'success'
						})
						this.closeModal();
						this.loadData();
					} else {
						app.isLogin(res.code, res.msg);
					}
				})
			})
		},
		del(id){
			var _this=this;
			uni.showModal({
				title: '提示',
				content: '确认要删除该条报价？',
				success: function(res) {
					if (res.confirm) {
						_this.request({
							url: interfaces.orderDelPrice,
							data: {
								order_no:_this.order_no,
								id:id
							},
							header:{
								token:_this.master_info.token
							},
							method: 'POST',
							success: ((res) => {
								console.log(res);
								if (res.code == 200) {
									wx.showToast({
										title: res.msg,
										icon: 'success'
									})
									_this.loadData();
								} else {
									app.isLogin(res.code, res.msg);
								}
							})
						})
					}
				}
			})
		}
	}
}
